.App {
    width: 100%;
    height: 100vh;
    background: #870000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #190A05, #870000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #190A05, #870000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  }
  
  .carousel {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .carousel h2{
    text-transform: capitalize;
    color: #fff;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    font-family: 'Aclonica', sans-serif;
  }
  
  .card {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 350px;
    height: 525px;
    object-fit: cover;
    cursor: pointer;
    z-index: 0;
    opacity: 0;
    transition: 500ms;
  }
  
  .card--active {
    opacity: 1;
    transform: scale(1);
    z-index: 99;
  }
  
  .card--left {
    transform: translateX(-125%) scale(0.8);
    transition: 500ms;
    opacity: 0.3;
  }
  
  .card--right {
    transform: translateX(125%) scale(0.8);
    transition: 500ms;
    opacity: 0.3;
  }

  